import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue'
import commonList from '@/components/commom/common-list.vue'
import loadMore from '@/components/commom/load-more.vue'
const demo = [
	{
		username: '陈子轩',
		userpic: '../../static/default.jpg',
		newstime: '2020-5-20 下午05:20',
		isFollow: true,
		title: '成功鄙视偶然',
		titlepic: '../../static/demo/datapic/11.jpg',
		support: {
			type: 'support',
			support_count: 1,
			unsupport_count: 2,
		},
		comment_count: 2,
		share_num: 2
	},
	{
		username: '陈子轩',
		userpic: '../../static/default.jpg',
		newstime: '2020-5-20 下午05:20',
		isFollow: true,
		title: '我是标题啊',
		titlepic: '../../static/demo/datapic/1.jpg',
		support: {
			type: 'unsupport',
			support_count: 100,
			unsupport_count: 2,
		},
		comment_count: 2,
		share_num: 2
	}, {
		username: '陈子轩',
		userpic: '../../static/default.jpg',
		newstime: '2020-5-20 下午05:20',
		isFollow: true,
		title: '我是标题啊',
		titlepic: '',
		support: {
			type: '',
			support_count: 100,
			unsupport_count: 0,
		},
		comment_count: 2,
		share_num: 0
	}
]
export default {
	components: {
		uniNavBar,
		commonList,
		loadMore
	},
	data() {
		return {
			scrollH: 500,
			tabIndex: 0,
			tabBars: [
				{
					name: '关注'
				},
				{
					name: '话题'
				}
			],
			list: [],
			loadmore: '下拉加载更多'
		}
	},
	onLoad() {
		this.scrollHeight()
		// 加载数据
		this.list = demo
	},
	methods: {
		// 获取屏幕高度
		async scrollHeight() {
			let res = await uni.getSystemInfoSync()
			this.scrollH = res.windowHeight - res.statusBarHeight - 44
			
		},
		// 切换选项卡
		changeTab(e) {
			this.tabIndex = e
		},
		// 切换
		onChangeTab(e) {
			this.tabIndex = e.detail.current
		},
		// 打开发布页
		openAddInput() {
			uni.navigateTo({
				url: '../add-input/add-input'
			})
		},
		// 顶踩
		doSupport(e) {
			let { type, index } = e
			// 拿到当前对象
			let item = this.list[index].support
			let msg = type === 'support' ? '顶' : '踩'
			// 之前没有操作
			if(item.type === '') {
				item[type + '_count']++
			} else if(item.type === 'support' && type === 'unsupport' ) { // 之前顶了
				// 踩 + 1
				item.unsupport_count++
				// 顶 + 1
				item.support_count--
			} else if(item.type === 'unsupport' && type === 'support') { // 之前踩了
				// 顶 + 1
				item.support_count++
				// 踩 + 1
				item.unsupport_count--
			}
			item.type = type
			uni.showToast({
				title: msg + '成功'
			})
		},
		// 加载更多
		loadMore() {
			// 验证当前是否可加载更多
			if(this.loadmore !== '上拉加载更多') return
			this.loadmore = '加载中...'
			setTimeout(() => {
				this.list = [...this.list, ...this.list]
				this.loadmore = '下拉加载更多'
			}, 2000)
		}
	}
}
